:root {
  --transition: 0.3s ease-in-out;
  --base-black: #000000;
  --base-white: #FFFFFF;
  --main-blue: #1955DE;
}

html {
  font-size: 16px;
  overflow-x: hidden;
}

html {
  overflow: auto;
  background: #080808;
}

body {
  position: relative;
  min-width: 320px;
  color: #F8F8F3;
  font-family: "Playfair Display", serif;
  -webkit-font-smoothing: subpixel-antialiased;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  backface-visibility: hidden;
  text-rendering: geometricPrecision;
  font-optical-sizing: auto;
}

* {
  outline: none;
  word-wrap: break-word;
  scroll-behavior: smooth;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
a:focus,
a:hover {
  text-decoration: none;
}

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

hr {
  margin: 0;
  border: 1px solid var(--web-gray-30-color);
}

img {
  border: 0;
  max-width: 100%;
  vertical-align: middle;
}

button {
  padding: 0;
  cursor: pointer;
}

input,
button,
textarea {
  border-radius: 0;
  -webkit-appearance: none;
}

select {
  -webkit-appearance: none;
}

.lenis.lenis-smooth {
  scroll-behavior: auto;
}
.lenis.lenis-smooth [data-lenis-prevent] {
  overscroll-behavior: contain;
}
.lenis.lenis-stopped {
  overflow: hidden;
}
.lenis.lenis-scrolling iframe {
  pointer-events: none;
}

/*.txt-anim {
  overflow: hidden;
}*/
span.char {
  position: relative;
  z-index: 1;
}

.only-dt {
  display: none;
}

.main-btn {
  display: inline-flex;
  gap: 0.5rem;
  align-items: center;
  justify-content: center;
  min-height: 2.875rem;
  padding: 0.3125rem 1.5rem;
  font-family: "Helvetica", sans-serif;
  color: #080808;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1;
  background-color: #F8F8F3;
  border: 1px solid #F8F8F3;
  border-radius: 0.125rem;
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
}
.main-btn.tp-1 {
  color: #F8F8F3;
  background-color: #080808;
  border-color: #080808;
}
h1 {
  margin: 0;
  font-size: 2.8125rem;
  font-weight: 400;
  line-height: 1;
  letter-spacing: -0.0625rem;
}

h2 {
  margin: 0;
  font-size: 2.25rem;
  font-weight: 400;
  line-height: 1.3334;
  letter-spacing: -0.0625rem;
}

h3 {
  margin: 0;
  font-size: 2.5rem;
  font-weight: 400;
  line-height: 1.325;
  letter-spacing: -0.0625rem;
}

p {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 400;
  line-height: 1.333;
  letter-spacing: -0.0625rem;
}

